export const statusConvert = {
    getStatusColor: function(status) {
        switch (status) {
            case 0:
                return "red";
            case 1:
                return "orange";
            case 2:
                return "yellow";
            case 3:
                return "green";
            case 4:
                return "blue";
            case 5:
                return "purple";
        }
    },
    getStatusTitle: function(status) {
        switch (status) {
            case 0:
                return "";
            case 1:
                return "";
            case 2:
                return "";
            case 3:
                return "";
            case 4:
                return "";
            case 5:
                return "";
        }
    },
    getStatusTextColor: function(status) {
        switch (status) {
            case 0:
                return "white";
            case 1:
                return "white";
            case 2:
                return "black";
            case 3:
                return "white";
            case 4:
                return "white";
            case 5:
                return "white";
            default:
                return "white";
        }
    },
};